@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding: 40px 100px ;
  font-family: 'Montserrat', sans-serif;
}
@media screen and (max-device-width : 480px) {
  .App {
    padding: 20px 20px ;
  }
}

header{
    display: flex;
    justify-content: space-between;
    height: 68px;
}
header img{
    height: 100%;
    cursor: pointer;
}
header ul{
    display: flex;
    justify-content: space-between;
    list-style-type: none; 
}
header li{
    text-align: right;
    width: 150px;
    
}
header a{
    color: #FF6060;
    font-size: 24px;
    text-decoration:none;
    border-bottom: 2px solid transparent;
}
header a:hover{
    border-color: #FF6060;
 }
@media screen and (max-device-width : 480px) {
    header{
        height: 47px;
    }
    header li{
        width: 70px;
    }
    header a{
        font-size: 12px;
    }
}
.Home .thumbs-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    grid-gap: 50px;
    gap: 50px;
    justify-content: center;
    padding: 56px 50px;
    background: #F6F6F6;
    border-radius: 25px;
  }
  @media screen and (max-device-width : 480px) {
    .Home .thumbs-container{
      background: none;
      padding: 0;
      grid-gap: 20px;
      gap: 20px;
      margin-top: 20px;
    }
  }
  
.Banner {
    height: 223px;
    border-radius: 15px;
    text-align: center;
    background-size: cover;
}
.Banner .opacity {
    background-color: black;
    opacity: 0.3;
    border-radius: 15px;
    height: 100%;
}
.Banner .message-banner {
    font-size: 48px;
    color: white;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 1;
}
.Banner .message-banner .opacity {
    position: absolute;
    z-index: -1;
    background-color: black;
    opacity: 0.3;
    height: 100%;
    width: 100%;
    border-radius: 15px;
}
@media screen and (max-device-width : 480px) {
    .Banner {
        height: 111px;
        width: 335px;
    }
    .Banner .message-banner {
        font-size: 24px;
        text-align: left;
        padding-left: 16px;
        padding-right: 102px;
    }
    .Banner .message-banner .opacity{
        margin-left: -16px;
    }
}
.Thump{
    width:340px;
    height:340px;
    border-radius: 10px;
    position: relative;
    background-size: cover;
    cursor: pointer;
}
.Thump .title{
    font-size: 18px;
    color: white;
    position: absolute;
    bottom: 20px;
    left: 20px;
    width: 50%;
}
@media screen and (max-device-width : 480px) {
    .Thump{
        height:255px;
        width:335px;
    }
}
.collapse-container {
    padding: 0 108px;
}
.Banner-phone{
    display: none;
}
@media screen and (max-device-width : 480px) {
    .collapse-container {
        padding: 0 ;
    }
    .Banner-desktop{
        display: none;
    }
    .Banner-phone{
        display: block;
    }
}
.Collapse{
    width: 100%;
}
.Collapse .header{
    height: 47px;
    background: #FF6060;
    border-radius: 5px;
    font-size: 24px;
    color: #fff;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 18px;
    cursor: pointer;
    position: relative;
}
.Collapse .body{
    font-size: 18px;
    padding: 1px 18px 19px 18px;
    background: #F7F7F7;
    color: #FF6060;
    font-weight: normal;
}
.Collapse .arrow{
    height: 14px;
    position: absolute;
    right: 11px;
}
.Collapse li{
    text-align: right;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 18px;   
}
.Collapse .arrow.up {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.Collapse .arrow.down {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
@media screen and (max-device-width : 480px) {
    .Collapse .header{
        width:90%;
        font-size: 13px;
    }
    .Collapse .body{
        font-size: 12px;
    }
}
footer{
    position: relative;
    height: 209px; 
    background: #000000;
    width: 100% !important;
}
footer .logo{
   text-align: center;
   padding-top: 66px;
}
footer .text-footer{
    color: #fff;
    font-size: 24px;
    text-align: center;
}
.NotFound .error404{
    font-size: 288px;
    text-align: center;
    color: #FF6060;
}
.NotFound .error-message{
    font-size: 36px;
    text-align: center;
    color: #FF6060;
}
 .linkToHome a  {
     display: block;
    font-size: 18px;
    text-align: center;
    color: #FF6060;
    text-decoration:underline;
}
@media screen and (max-device-width: 480px) {
    .NotFound .error404{
        margin-top: 150px;
        font-size: 96px;
    }
    .NotFound .error-message{
        margin-bottom: 80px;
        font-size: 18px;
    }
    .NotFound .linkToHome a {
        font-size: 14px;
        text-align: center;
        color: #FF6060;
        text-decoration:underline;
    }
}
.HousingSheet .HousingSheet-collapse {
    display: flex;
    grid-column-gap: 7%;
    -webkit-column-gap: 7%;
            column-gap: 7%;
}
.HousingSheet .title {
    font-size: 36px;
    color: #ff6060;
    margin: 0;
}
.HousingSheet .location {
    font-size: 18px;
    color: #ff6060;
}
.title-host{
    display: flex;
    justify-content: space-between;
}
.HousingSheet .equipments {
    display: flex;
    flex-direction: column;
    padding-left: 0;
}
.equipments .equipment {
    text-align: left;
    list-style-type: none;
}
.HousingSheet .tags-ratings {
    display: flex;
    justify-content: space-between;
}
.rating-host-phone{
    display: none;
}
@media screen and (max-device-width: 480px) {
    .HousingSheet{
        position: relative;
    }
    .HousingSheet .title {
        font-size: 18px;
    }
    .rating-host-phone {
        display: flex;
        justify-content: space-between;
    }
    .HousingSheet .tags-ratings {
        display: block;
    }
    .HousingSheet .HousingSheet-collapse {
        display: block;
    }
    .HousingSheet .location {
        font-size: 14px;
    }
    .equipments .equipment {
        font-size: 12px;
    }
    .host-desktop {
        display: none;
    }
    .tags-ratings .Rating{
        display: none;
    }
    
}

.Carrousel {
    height: 415px;
    position: relative;
}
.Carrousel .carrousel-picture {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 25px;
}
.picturenumber {
    position: absolute;
    bottom: 24px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
    color: #fff;
}
.Carrousel button {
    border: none;
    width: 46px;
    height: 79px;
    background: transparent;
}
.Carrousel .button-left {
    position: absolute;
    left: 20px;
    bottom: 40%;
}
.Carrousel .button-right {
    position: absolute;
    right: 32px;
    bottom: 40%;
}
.Carrousel .button-phone {
    display: none;
}
@media screen and (max-device-width: 480px) {
    .Carrousel .button-phone {
        display: block;
    }
    .Carrousel .button-desktop {
        display: none;
    }
    .Carrousel {
        height: 255px;
    }
    .picturenumber {
        display: none;
    }
    .Carrousel .button-left {
        bottom: 33%;
    }
    .Carrousel .button-right {
        right: 0px;
        bottom: 33%;
    }
}

.Tag{
    display: flex;
    justify-content: left;
    grid-column-gap: 2%;
    -webkit-column-gap: 2%;
            column-gap: 2%;
    grid-gap: 5px;
    gap: 5px;
    padding-left: 0;
    flex-wrap: wrap;
}
.tag {
    background-color: #FF6060;
    height: 25px;
    border-radius: 10px;
    border-color: #FF6060;
    border-style: solid;
    color: #fff;
    text-align: center;
    padding: 2px 19px;
    font-size: 14px;
    padding-top: 6px;
    list-style-type: none;
    width: 150px;
}
@media screen and (max-device-width: 480px) {
    .tag{
        height: 18px;
        font-size: 10px;
        height: 15px;
        padding: 2px 7px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
}

.Host{
    display: flex;
}
.Host .host-image {
    border-radius: 50%;
    max-height: 64px;
}
.Host .host-name{
    font-size: 18px;
    color: #FF6060;
    width: 93px;
    text-align: center;
    padding-top: 10px;
    max-height: 64px;
}
@media screen and (max-device-width: 480px) {
    .Host {
        display: flex;
    }
    .Host .host-image {
        
        height: 32px;
    }
    .Host .host-name{
        font-size: 12px;
        width: 83px;
        padding-top: 1px;
    }
}

.rating-star{
    width: 30px;
}
@media screen and (max-device-width: 480px) {
   .Rating .rating-star{
        width: 15px;
        height: 15px;
    }  
}

